.backdrop {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: $modal-background;
	opacity: 1;
	overflow-y: scroll;
	z-index: 1;
	// transition: opacity $time $time-function;
	&.is-hidden {
	  opacity: 0;
	  pointer-events: none;
	
}
 }
 
 .modal {
	 width: 1175px;
	 margin: 50px auto;
	position: relative;
	padding: 40px;
	background-color: $primary-background-color;
	// box-shadow: $shadow-modal;
	border-radius: 4px;
 &-subtitle {
	@include adaptiv-font (30,20);
	padding-top: 18px;
	padding-bottom: 7px;
 }
 &-list {
	 display: flex;
	@include adaptiv-font(58, 30);
	padding-left: 2rem;
	
 }
 &-list:nth-child(1) {
	border-bottom: 1px solid $thin-line-color;
 }
 &-list:nth-child(1):before {
	 content: "1";
	 color: $button-accent-color;
	 padding-right: 14px;
 }
 &-list:nth-child(2):before {
	content: "2";
	color: $button-accent-color;
	padding-right: 14px;
}
&-title {
	@include font-size(normal, 800, normal);
	@include adaptiv-font(30, 20);
	margin-bottom: 30px;
}
	// transition: transform $time $time-function $time;
	&__title {
	//   @extend %nomargin;
	  margin-bottom: 12px;
	//   @include typography(700, 20px, 1.15);
	  text-align: center;
	}
 
	&__button-close {
	//   @include flexbox(center, center);
	  position: absolute;
	  top: 8px;
	  right: 8px;
	  padding: 0;
	  margin: 0;
 
	  width: 30px;
	  height: 30px;
	  border-radius: 50%;
	  background-color: $button-accent-color;
	  border: 1px solid $thin-line-color;
	//   transition: fill $time $time-function, transform $time $time-function;
	}
 
	&__button-close:hover {
	  fill: $accent-color;
	  outline-color: $accent-translucent-color;
	  transform: scale(1.1);
	}
.container-subtitle {
	 padding-left: 120px;
	 margin-bottom: 60px;
 }
	.backdrop.is-hidden & {
	  transform: translate(-50%, -50%) scale(0);
	}
	// @include for-size(mobile-only) {
	//   transform: scale(1);
	//   max-width: 450px;
	// }
 
	// @include for-size(mobile) {
	//   position: absolute;
	//   width: 450px;
	//   left: 50%;
	//   transform: translateX(-50%) scale(1);
	// }
 
	// @include for-size(desktop) {
	//   top: 221px;
	//   width: 528px;
	// }
 }
 fieldset {
	 border: none;
 }
//  input[type="radio" i] {
// 	 color: $button-accent-color;
// 	 background-color: $button-accent-color;
//  }
 .visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	clip: rect(0 0 0 0);
	overflow: hidden;
 }
 .form-inputs {
	outline: none;
	 &__item {
	height: 60px;
 }
&__field {
	position: relative;
	flex-direction: column;
 }
 &__list {
	text-align: start;
	margin-bottom: 20px;
 }
&__label {
		@include adaptiv-font(12, 6);
	width: 300px;
	height: 30px;
	margin-bottom: 4px;
 }
&__label::placeholder {
	padding-left: 8px;
}
}

.form-input:focus-within {
	border-color: $button-accent-color;
 }
 .form-modal-field:focus-within .form-modal-icon {
	fill: $button-accent-color;
 }